import React, { Component } from 'react';
import './App.css';

import {
  Input,
  Button,
  Checkbox,
} from 'antd';

const { TextArea } = Input;

class App extends Component {
  handleButtonClick() {
    let oTextarea1 = document.getElementById('oTextarea1');
    let oTextarea2 = document.getElementById('oTextarea2');
    let oCheckbox = document.getElementById('oCheckbox');

    var sRemoveHtml = oTextarea1.value.replace(/<[^>]+>/ig, '');
    oTextarea2.value = oCheckbox.checked ? sRemoveHtml.replace(/\s*/g, '') : sRemoveHtml;
  }
  
  render() {
    return (
      <div className="wrapper">
        <div className="main">
          <h1 className="title">clear html tag</h1>
        
          <div className="textarea_wrapper">
            <div className="textarea">
              <div>Before</div>
              <TextArea id="oTextarea1" rows="14" />
            </div>

            <div className="textarea">
              <div>After</div>
              <TextArea id="oTextarea2" rows="14" />
            </div>
          </div>

          <div className="button_wrapper">
            <Checkbox id="oCheckbox"><span style={{fontSize: '20px'}}>remove whiteblank</span></Checkbox>
            <Button className="ok_button" type="primary" onClick={this.handleButtonClick}>OK</Button>
          </div>
        </div>

        <div className="bottom">
          {/* eslint-disable-next-line */}
          Powered <div className="bottom_heart">❤️</div> liuyibo
        </div>
      </div>
    );
  }
}

export default App;
